"use client"

import React, { useState, useEffect } from 'react';
import { useRoute } from 'next/router';  // Changed to use useRoute
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from "@/components/ui/accordion";
import { BookDetails, fetchBookDetails } from '@/app/models/bookDetails';

export default function BookDetailsPage() {
  const route = useRoute();  // Using useRoute here
  const { id } = route.query;  // Get the id from route.query instead of useParams

  const [book, setBook] = useState<BookDetails | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const getBookDetails = async () => {
      if (typeof id === 'string') {
        const details = await fetchBookDetails(id);
        setBook(details);
        setLoading(false);
      }
    };
    getBookDetails();
  }, [id]);

  if (loading) {
    return (
      <div className="min-h-screen bg-black text-white p-8">
        <div className="max-w-6xl mx-auto">
          <div className="animate-pulse">
            <div className="h-8 bg-gray-700 w-1/3 mb-4 rounded"></div>
            <div className="h-4 bg-gray-700 w-1/4 mb-8 rounded"></div>
            <div className="h-40 bg-gray-700 w-full rounded"></div>
          </div>
        </div>
      </div>
    );
  }

  if (!book) {
    return (
      <div className="min-h-screen bg-black text-white p-8">
        <div className="max-w-6xl mx-auto">
          <p className="text-red-500">Book not found</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-black text-white p-8">
      <div className="max-w-6xl mx-auto">
        <div className="grid grid-cols-1 md:grid-cols-[400px,1fr] gap-8">
          {/* Book Cover */}
          <div>
            <img
              src={book.attributes.cover}
              alt={book.attributes.title}
              className="w-full h-auto rounded-lg"
            />
          </div>

          {/* Book Details */}
          <div>
            <h1 className="text-3xl font-bold mb-2">{book.attributes.title}</h1>
            <p className="text-xl text-gray-400 mb-4">{book.attributes.author}</p>
            <div className="flex gap-8 text-gray-400 mb-6">
              <p>Pages: {book.attributes.pages}</p>
              <p>Released: {book.attributes.release_date}</p>
            </div>
            <p className="text-gray-300 mb-8">{book.attributes.summary}</p>
            
            {/* Chapters Section */}
            <div className="mt-8">
              <h2 className="text-2xl mb-4">Chapters</h2>
              <div className="space-y-2">
                {book.attributes.chapters.map((chapter, index) => (
                  <Accordion
                    key={chapter.id}
                    type="single"
                    collapsible
                    className="border-b border-gray-800"
                  >
                    <AccordionItem
                      value={chapter.id}
                      className={!chapter.attributes.content ? 'opacity-50' : ''}
                    >
                      <AccordionTrigger
                        disabled={!chapter.attributes.content}
                        className="hover:no-underline py-4 text-left"
                      >
                        <span className="flex items-center text-gray-300">
                          Chapter {index + 1}: {chapter.attributes.title}
                        </span>
                      </AccordionTrigger>
                      <AccordionContent className="text-gray-400 pb-4">
                        {chapter.attributes.content || 'No content available'}
                      </AccordionContent>
                    </AccordionItem>
                  </Accordion>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
